import React from "react";
import { graphql } from "gatsby";
import Layout from "../components/layout";
import PostList from "../components/post-list";
import styled from "styled-components";
import StyledLink from "../components/styled-link";
// import { openPopupWidget } from "react-calendly";

const HomePage = ({ data }) => {
  // const posts = data.allMarkdownRemark.nodes;
  const intro = data.markdownRemark.html;
  const {
    url,
    cta_mail,
    cta_call,
    header,
    subheader,
    cta_button,
    cta_under_button,
    header2,
    subheader2,
  } = data.markdownRemark.frontmatter;

  console.log(data.markdownRemark);
  return (
    <Layout>
      <MainContainer>
        <TitleLabel>{header}</TitleLabel>
        <SubtitleLabel>{subheader}</SubtitleLabel>

        <Intro>
          <div class="button-container">
            <a href="mailto:info@improove.fit">
              <LemonadeOutlineButton>{cta_mail}</LemonadeOutlineButton>
            </a>
            <a
              target="_blank"
              rel="noopener noreferrer"
              href="https://calendly.com/improove-trainer/15min"
            >
              <LemonadeButton>{cta_call}</LemonadeButton>
            </a>
          </div>
        </Intro>
        <SubButton>{cta_under_button}</SubButton>
      </MainContainer>

      {/* <PostList posts={posts} /> */}
      {/* <StyledLink
        css={`
          display: block;
          margin-top: var(--size-800);
          margin-bottom: var(--size-800);
          margin-left: auto;
          margin-right: auto;
          width: fit-content;
        `}
        to="/blog"
      >
        View All posts
      </StyledLink> */}
    </Layout>
  );
};

export default HomePage;
const MainContainer = styled.div`
  // backdrop-filter: blur(16px) saturate(180%);
  // -webkit-backdrop-filter: blur(16px) saturate(180%);
  // background-color: rgba(17, 25, 40, 0.75);
  // border-radius: 12px;
  // border: 1px solid rgba(255, 255, 255, 0.125);
  // padding: 0.5em 0.7em;
  padding-bottom: 2em;
  flex-wrap: wrap;
  display: flex;
  justify-content: space-around;
  align-content: center;
  flex-direction: column;
  @media screen and (max-width: 800px) {
    flex-direction: column;
    align-items: center;
  }
  max-width: min(1300px, 100%);
  margin-right: auto;
  margin-left: auto;
  ul {
    position: relative;
    list-style: none;
    margin-left: 0;
    padding-left: 1.2em;
  }
  ul li {
    padding: 5px 5px;

    font-weight: 400;
    line-height: 23px;
  }
  ul li:before {
    content: url(/media/signs.svg);
    position: absolute;
    left: 0;
    // width: 16px;
  }
`;

const Cat = styled.div`
  margin-left: auto;
  margin-right: auto;
  margin-top: 30px;
  margin-bottom: 30px;
  width: 70px;
  content: url(/media/cat.svg);
`;

const Intro = styled.div`
  display: flex;
  flex: 3;
  flex-direction: column;
  max-width: min(60ch, 100%);
  // align-items: center;
  margin-right: auto;
  margin-left: auto;
  // margin-top: var(--size-800);
  // margin-bottom: var(--size-900);
  // text-align: center;
  // min-width: 500px;
  padding: 0 3em;

  & p {
    // text-transform: capitalize;
    font-size: var(--size-400);
  }
  & h1 {
    padding-bottom: 0.5em;
  }
  & .button-container {
    display: flex;
    flex-wrap: wrap;
    & a.store-link {
      // flex: 0 1;
      height: 40px;
      width: 150px!important;
      & span {
        height: 40px;
        max-width: 150px!important;
        margin: unset;
        // height: 40px;
        // width: auto;
        box-shadow: none;
        & img {
          margin: unset;
          object-fit: contain;
        }
        }
      }
    }
    & .btn {
      text-decoration: none;
      text-align: center;
      padding: 10px 20px;
      margin: 5px;
      font-size: 18px;
      font-weight: 900;
      color: white;
      background-color: #373856;
      border: 2px solid #373856;
      border-radius: 5px;
      cursor: pointer;
      width: max-content;
      transition: 0.25s ease;
      &-1:hover {
        border-style: dashed;
      }
      &-2:hover {
        border-radius: 25px;
      }
      &-3:hover {
        color: white;
        background-color: transparent;
      }
      &-4:hover {
        box-shadow: 0 6px 18px 0 rgba(#000, 0.1);
        color: $primary;
        border-color: transparent;
        background-color: transparent;
      }
      &-5:hover {
        box-shadow: 0 6px 18px 0 rgba(#000, 0.1);
        transform: translateY(-6px);
      }
      &--transparent {
        color: $primary;
        background-color: transparent;
      }
    }
    & .btn-outline{
      border: 2px solid #373856;
      border-radius: 5px;
      background-color: white;
      color: #373856;
    }
    
  }
  @media screen and (max-width: 1200px) {
    padding: 0 1.5em;
    & .btn {
      margin: 5px;
      font-size: 15px;
      padding: 8px 16px;
    }

  }

  @media screen and (max-width: 800px) {
    // margin-top: 1.5em;
    // margin-bottom: 1.5em;
    min-width: unset;
    & h1 {
      text-align: center!important;
      font-weight: 400;
      line-height: 36px;
      font-size: 2.0em;
    }
    & h4 {
      text-align: center!important;
      font-weight: 400;
      line-height: 28px;
    }
    & .button-container{
      justify-content: center;
      & .btn{
        flex: 0 1;
        min-width: min(300px,90%);
        font-size: 18px;
        text-transform: uppercase;
        margin: 8px;
        font-size: 15px;
        padding: 14px 20px;
      }
      
    }
  }
`;

